import React, { useState, useRef } from 'react';
import { Link } from 'react-router-dom';
import emailjs from 'emailjs-com';
import './ContactPage.css';

const ContactPage = () => {
  // Correctly typed useRef for an HTMLFormElement
  const formRef = useRef<HTMLFormElement>(null);

  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    message: ''
  });

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { id, value } = e.target;
    setFormData(prev => ({ ...prev, [id]: value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (formRef.current) {
      // Use emailjs.sendForm if formRef.current is available
      emailjs.sendForm(
        'service_f5sxen4',
        'template_dp8mqgr',
        formRef.current,
        'tyyCV9g6FoxoNQZ0s'
      )
      .then((result) => {
        console.log('SUCCESS!', result.text);
        alert('Message sent successfully!');
        formRef.current?.reset(); // reset the form if you like
      }, (error) => {
        console.log('FAILED...', error.text);
        alert('Failed to send message. Please try again later.');
      });
    }

    // Clear local state
    setFormData({
      name: '',
      email: '',
      phone: '',
      message: ''
    });
  };

  return (
    <>
      <section className="contact-section">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-md-8">
              <div className="text-center mb-5">
                <h1 className="display-4">Get Involved</h1>
                <p className="lead">Join us in building a stronger Madison Township</p>
              </div>

              <div className="row g-4">
                {/* Contact Form */}
                <div className="col-md-7">
                  <div className="contact-card">
                    <h3 className="mb-4">Send Us a Message</h3>
                    <p>Let's collaborate on improving Madison Township's future!</p>
                    <form ref={formRef} onSubmit={handleSubmit}>
                      <div className="mb-3">
                        <label htmlFor="name" className="form-label">Full Name</label>
                        <input
                          type="text"
                          className="form-control"
                          id="name"
                          name="fullName"
                          value={formData.name}
                          onChange={handleInputChange}
                          required
                        />
                      </div>
                      <div className="mb-3">
                        <label htmlFor="email" className="form-label">Email Address</label>
                        <input
                          type="email"
                          className="form-control"
                          id="email"
                          name="email"
                          value={formData.email}
                          onChange={handleInputChange}
                          required
                        />
                      </div>
                      <div className="mb-3">
                        <label htmlFor="phone" className="form-label">Phone Number</label>
                        <input
                          type="tel"
                          className="form-control"
                          id="phone"
                          name="number"
                          value={formData.phone}
                          onChange={handleInputChange}
                        />
                      </div>
                      <div className="mb-3">
                        <label htmlFor="message" className="form-label">Your Message</label>
                        <textarea
                          className="form-control"
                          id="message"
                          rows={4}
                          name="message"
                          value={formData.message}
                          onChange={handleInputChange}
                          required
                        ></textarea>
                      </div>
                      <button type="submit" className="btn btn-primary">Send Message</button>
                    </form>
                  </div>
                </div>

                {/* Contact Info */}
                <div className="col-md-5">
                  <div className="contact-info-card">
                    <h3 className="mb-4">Contact Information</h3>
                    <p><strong>Phone:</strong><br /> (614) 439-1930</p>
                    <p><strong>Email:</strong><br /> animelycandidate@gmail.com</p>
                    <hr />
                    <h4 className="mt-4">Campaign Office</h4>
                    <p>Friends of Andrew<br />
                      3237 Boxwood Ct. N. <br />
                      Groverport, Ohio 43125</p>
                  </div>
                </div>
              </div>

              {/* Volunteer Options */}
              <div className="volunteer-options mt-5">
                <h3 className="text-center mb-4">Ways to Help</h3>
                <div className="row g-4">
                  <div className="col-md-4">
                    <div className="text-center">
                      <h4>Yard Signs</h4>
                      <p>Display your support with a campaign yard sign</p>
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="text-center">
                      <h4>Door Knocking</h4>
                      <p>Help spread our message throughout the township</p>
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="text-center">
                      <h4>Fundraising</h4>
                      <p>Support our campaign's financial goals</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default ContactPage;
